@import '../../scss/mixin';


.game__window{
  @include display-block;
  max-width: 750px;
  margin: 20px auto;

  .game__title{
    @include display-block;
    font: 600 40px/45px 'Inter', sans-serif;
    color: #F29F05;
    text-shadow: 1px 1px 0 #A64F03, 2px 2px 0 #A64F03,3px 3px 0 #A64F03,0 0 2px #000;
    text-align: center;
    margin-bottom: 20px;
  }

  .game__window__inner{
    @include display-block;
    padding: 20px;
    width: calc(100% - 8px);
    background: #F29F05;
    box-shadow:1px 1px 0 #A64F03,2px 2px 0 #A64F03,3px 3px 0 #A64F03,4px 4px 0 #A64F03,
    5px 5px 0 #593E25,6px 6px 0 #593E25,7px 7px 0 #593E25,8px 8px 0 #593E25, inset 0 0 4px #000;

    .game__resource{
      @include flex_row;
      @include justify-space-between;

      .game__resource__content{
        @include flex_row;
        width: auto;
        p{
          @include display-block;
          width: auto;
          font: 500 22px/26px  sans-serif;
          color: #1D594E;
          letter-spacing: 2px;
          text-shadow: 1px 1px 0 #1D594E, 1.5px 1.5px 0 #1D594E,2px 2px 0 #1D594E,0 0 2px #1D594E;
        }
        span{
          @include display-block;
          width: auto;
          font: 400 22px/26px 'Inter', sans-serif;
          color: #F23030;
          margin-left: 5px;
          letter-spacing: 2px;
          text-shadow: 1px 1px 0 #F23030, 1.5px 1.5px 0 #F23030,2px 2px 0 #F23030,0 0 2px #F23030;
        }
      }
    }

    .game__body{
      @include display-block;
      padding-top: 100%;
      margin-top: 20px;
      .game__body__inner{
        @include flex_row;
        flex-wrap: wrap;
        position: absolute;
        inset: 0;
        // background: #3e3e42;
        background: #206058;
        box-shadow: inset 0 0 15px #061412,inset 0 0 10px #000;
        padding: 10px;
        .mouse__hole{
          @include display-block;
          width: 33.3%;
          padding-top: 33.3%;
          .mouse__hole__inner{
            @include flex_row;
            width: auto;
            position: absolute;
            inset: 10px;
            padding: 10px;
            border-radius: 50%;
            border: 3px solid #000;
            background: #061412;
            cursor: pointer;
            overflow: hidden;
            transition: none;
            -webkit-tap-highlight-color: transparent;


            &:after{
              position: absolute;
              display: block;
              content: "";
              inset: 0;
              border-radius: 50%;
              box-shadow: inset 0 0 10px 4px #174741,inset 0 -10px 15px #287A70;
            }

            img{
              @include display-block;
              @include user-select-none;
              width: 80%;
              margin-top: 210%;
              object-fit: contain;
              transition: margin-top 0.2s ease;
              &.active{
                margin-top:20%;
              }
            }
          }
        }

        .game__result{
          @include flex-row;
          position: absolute;
          height: 100%;
          z-index: 1;
          &:after{
            position: absolute;
            display: block;
            content: " ";
            inset: 0;
            background: rgba(0, 0, 0, .6);
            filter: blur(10px);
            z-index: -1;
          }
          .game__result__inner{
            @include flex-column;
            @include justify-center;
            @include align-center;
            width: 50%;
            min-height: 200px;
            padding: 20px;
            background: #192734;
            border: solid 2px #C9463D;
            border-radius: 10px;
            background: #962E40;
            box-shadow:1px 1px 0 #5E1742,2px 2px 0 #5E1742, inset 0 0 5px #000;
          }

          p{
            @include display-block;
            width: auto;
            margin-bottom: 20px;
            font: 600 35px/40px 'Inter', sans-serif;
            color: #6BBFBF;
            text-shadow: 1px 1px 0 #1D594E, 1.5px 1.5px 0 #1D594E,2px 2px 0 #1D594E,0 0 2px #1D594E;
          }
          span{
            @include display-block;
            width: auto;
            margin-bottom: 20px;
            font: 700 50px/55px 'Inter', sans-serif;
            color: #FFBD00;
            text-shadow: 1px 1px 0 #E6880A, 2px 2px 0 #E6880A,3px 3px 0 #E6880A,0 0 6px #000;

          }
          button{
            @include display-block;
            width: auto;
            padding: 6px 40px;
            font: 700 28px/34px 'Inter', sans-serif;
            color: #243773;
            background: #F29B30;
            border:solid 2px #C1700C;
            border-radius: 6px;
            box-shadow: 3px 3px 2px 0px #000,inset 0 0 3px #000;
            &:hover{
              color: #3856b4;
            }
          }
        }

      }
    }
  }

  @media(max-width: 768px){
    max-width: inherit;
    width: calc(100% - 20px);
    margin: 0 10px;
    padding-top: calc(50vh - 50vw - 50px);

    .game__title{
      font: 600 35px/40px 'Inter', sans-serif;
      text-shadow: 1px 1px 0 #A64F03, 2px 2px 0 #A64F03,0 0 2px #000;
      margin-bottom: 10px;
    }
    .game__window__inner{
      width: calc(100% - 7px);
      padding: 10px;
      box-shadow:1px 1px 0 #A64F03,2px 2px 0 #A64F03,3px 3px 0 #A64F03,
      5px 5px 0 #593E25,6px 6px 0 #593E25,7px 7px 0 #593E25, inset 0 0 3px #000;

      .game__resource{
        .game__resource__content{
          p{
            font: 500 20px/24px  sans-serif;
            text-shadow: 1px 1px 0 #1D594E, 1.5px 1.5px 0 #1D594E,0 0 2px #1D594E;
          }
          span{
            font: 400 20px/24px 'Inter', sans-serif;
            text-shadow: 1px 1px 0 #F23030, 1.5px 1.5px 0 #F23030,0 0 2px #F23030;
          }
        }
      }

      .game__body{
        margin-top: 10px;
        .game__body__inner{
          box-shadow: inset 0 0 10px #061412,inset 0 0 6px #000;
          .mouse__hole{
            .mouse__hole__inner{
              inset: 5px;
              padding: 5px;
              border: 2px solid #000;
              &:after{
                box-shadow: inset 0 0 8px 3px #174741,inset 0 -5px 8px #287A70;
              }

              img{
                width: 85%;
              }
            }
          }

          .game__result{
            &:after{
              filter: blur(6px);
            }
            .game__result__inner{
              width: 90%;
              min-height: 180px;
              padding: 10px;
              border-radius: 6px;
              box-shadow:1px 1px 0 #5E1742,2px 2px 0 #5E1742, inset 0 0 3px #000;
            }

            p{
              margin-bottom: 15px;
              font: 600 30px/35px 'Inter', sans-serif;
            }
            span{
              margin-bottom: 10px;
              font: 700 45px/50px 'Inter', sans-serif;
              text-shadow: 1px 1px 0 #E6880A, 2px 2px 0 #E6880A,0 0 3px #000;
            }
            button{
              padding: 8px 40px;
              font: 700 24px/30px 'Inter', sans-serif;
              margin-bottom: 10px;
            }
          }

        }
      }
    }

  }
}
