@property --angle {
	syntax: '<angle>';
	initial-value: 0deg;
	inherits: false;
}
@property --stretch {
	syntax: '<number>';
	initial-value: 0;
	inherits: false;
}
@property --distance {
	syntax: '<percentage>';
	initial-value: 0%;
	inherits: false;
}

@-webkit-keyframes rotate-angle {
	from {--angle: 0deg;}
	to {--angle: 360deg;}
}
@keyframes rotate-angle {
	from {--angle: 0deg;}
	to {--angle: 360deg;}
}


@-webkit-keyframes stretch-bg {
	0%,100% {--stretch: 0;}
	50% {--stretch: 1;}
}
@keyframes stretch-bg {
	0%,100% {--stretch: 0;}
	50% {--stretch: 1;}
}

@-webkit-keyframes move-forward {
  from {--distance: 0;}
	to {--distance: 100;}
}
@keyframes move-forward {
	from {--distance: 0%;}
	to {--distance: 100%;}
}

@mixin nostyle-block{
  position: relative;
  display: block;
  width: 100%;
  padding: 0;
  margin: 0;
}
@mixin display-block($w:100%,$m:0,$p:0){
  position: relative;
  display: block;
  width: 100%;
  padding: 0;
  margin: 0;
}


@mixin flex-row{
	position: relative;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;

	-webkit-box-orient: horizontal;
	-webkit-box-direction: normal;
	-ms-flex-direction: row;
	flex-direction: row;

	-webkit-box-align: center;
	-ms-flex-align: center;
	align-items: center;

	-webkit-box-pack: center;
	-ms-flex-pack: center;
	justify-content: center;

	width: 100%;
	padding: 0;
	margin: 0;
}

@mixin flex-column{
	position: relative;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;

	-webkit-box-orient: vertical;
	-webkit-box-direction: normal;
	-ms-flex-direction: column;
	flex-direction: column;

	-webkit-box-pack: start;
	-ms-flex-pack: start;
	 justify-content: flex-start;

	-webkit-box-align: start;
	-ms-flex-align: start;
	align-items: flex-start;
	width: 100%;
  padding: 0;
  margin: 0;
}

@mixin justify-start{
	-webkit-box-pack: start;
	-ms-flex-pack: start;
	justify-content: flex-start;
}
@mixin justify-end{
	-webkit-box-pack: end;
	-ms-flex-pack: end;
	justify-content: flex-end;
}
@mixin justify-space-between{
	-webkit-box-pack: justify;
	-ms-flex-pack: justify;
	justify-content: space-between;
}
@mixin justify-center{
	-webkit-box-pack: center;
	-ms-flex-pack: center;
	justify-content: center;
}

@mixin align-start{
	-webkit-box-align: start;
	-ms-flex-align: start;
	align-items: flex-start;
}
@mixin align-center{
	-webkit-box-align: center;
	-ms-flex-align: center;
	align-items: center;
}


@mixin user-select-none{
	-webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -o-user-select: none;
  user-select: none;
}

@mixin ellipsis-text{
	overflow: hidden;
	white-space: nowrap;
	-o-text-overflow: ellipsis;
	text-overflow: ellipsis;
	word-wrap: break-word;
}



@mixin gradient-border-bg($c1:#ffffa2,$c2: #adf8fc,$deg:135deg){
	background: $c1;
	background-image: linear-gradient($deg,$c1, $c2);
}

@mixin gradient-dummy-bg($c1:#555,$c2: #888){
	background: $c1;
	background-image: linear-gradient(135deg,$c1, $c2);
	@supports (background:paint(something)){
		background-image:-webkit-gradient(linear,left top, right top,from($c1),color-stop($c2),color-stop($c2),to($c1));
		background-image:-o-linear-gradient(left,$c1 var(--distance),$c2 calc(var(--distance) + 5%),$c2 calc(var(--distance) + 25%),$c1 calc(var(--distance) + 30%));
		background-image:linear-gradient(90deg,$c1 var(--distance),$c2 calc(var(--distance) + 5%),$c2 calc(var(--distance) + 25%),$c1 calc(var(--distance) + 30%));
		background-image:linear-gradient(90deg,$c1 var(--distance),$c2 calc(var(--distance) + 5%),$c2 calc(var(--distance) + 25%),$c1 calc(var(--distance) + 30%));
		-webkit-animation: 3s move-forward linear infinite normal;
		animation: 3s move-forward linear infinite normal;
	}

}

@mixin gradient-text($c1:#ffffa2,$c2:#adf8fc,$deg:135deg){

  color: $c1;
	-webkit-text-fill-color: transparent;
	-webkit-background-clip: text !important;
	background-clip: text !important;

	background: $c1;
	background-image: linear-gradient($deg,$c1, $c2);

	@include user-select-none;
}



@mixin external-margin($h : 10px,$w : 10px){
  margin: $h $w;
  width: calc(100% - 2*$w);
  height: calc(100% - 2*$h);
}




@mixin scroll-bar($w:4px,$d:0px){
	-webkit-overflow-scrolling: touch;
	-ms-overflow-style: -ms-autohiding-scrollbar;
	 &::-webkit-scrollbar {
		width: $w;
		background: #00011d;
	}
	&::-webkit-scrollbar-track {
		box-shadow: inset 0 0 6px rgba(0, 0, 0, .3);
		margin: $d 0;
	}
	&::-webkit-scrollbar-thumb {
		background-color: #364a65;
	}
}

@mixin scroll-bar2($w:4px,$d:0px){
	@include scroll-bar($w,$d);
	 &::-webkit-scrollbar {
		background: transparent;
	}
}

@mixin scroll-bar3($h:4px){

	-webkit-overflow-scrolling: touch;
	-ms-overflow-style: -ms-autohiding-scrollbar;
	 &::-webkit-scrollbar {
		height: $h;
		background: #444;
		padding-bottom: 10px;
		display: block;
	}

	&::-webkit-scrollbar-thumb {
		background-color: #32CCBC;
	}
}

@mixin psuedo-bg($w:2px,$r:8px){
	&:before{
		position: absolute;
		display: block;
		content: '';
		background: #383050;
		top: $w;
		left: $w;
		right: $w;
		bottom: $w;
		border-radius: $r;
		z-index: 0;
	}

}
